  background-color: #333;
}

.main-navbar {
  height: 120px;
}

.center-element {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form-vertically-center {
  height: 100%;
}

.input-field {
  border-radius: 50px;
  height: 25px;
  margin-bottom: 10px;
  padding: 5px;
}

.input-button {
  border-radius: 50px;
  width: 70px;
  height: 25px;
}

